
<template>
	<view class="hd-container">
		<view class="sun-header">
			<image class="side" src="/static/images/icon/sidebar.png" @click="openSidebar" mode=""></image>
			<image class="logo" src="/static/images/icon/logo.png" mode=""></image>
			<image class="scan" src="/static/images/icon/scan.png" mode=""></image>
		</view>
		<view class="hd-btn test1" @click="getSNFun(1)">测试区域1</view>
		<view class="hd-btn test2" @click="getSNFun(2)">测试区域2</view>
		<view class="hd-btn test3" @click="getSNFun(3)">测试区域3</view>
		<view class="hd-btn test4" @click="getSNFun(4)">测试区域4-打开侧边栏</view>
		
		<sidebar v-show="showSidebar" :version="versionCode" :status="showSidebar" @close="closeSidebar"></sidebar>
	</view>
</template>

<script>
// #ifdef APP-PLUS
const sunmiPrint = uni.requireNativePlugin('Hd-PrinterModule');
// #endif
import sidebar from '@/components/sidebar/sidebar.vue'
export default {
	components:{sidebar},
	data() {
		return {
			stateMsg: '还未开始',
			dataResult: '',
			showSidebar:false,
			versionCode:123,
		};
	},
	onLoad() {
		// #ifdef APP-PLUS
		sunmiPrint.inItService((res) => {
			if (res.code === 200) {
				console.log(`inItService---`, JSON.stringify(res));
				this.stateMsg = res['message'];
				this.dataResult = res['data'];
			} else {
				this.dataResult = res['data'];
				this.stateMsg = res['message'];
			}
		});
		// #endif
	},
	methods: {
		closeSidebar(){
			this.showSidebar = false
		},
		getSNFun(index){
			uni.showToast({
				icon:'none',
				title:'点击了'+index
			})
			this.showSidebar = true
		},
		openSidebar(){
			uni.showToast({
				icon:'none',
				title:'点击了打开侧边栏按钮'
			})
			this.showSidebar = true
		},
		/* 获取P系列 */
		initView() {
			sunmiPrint.initView((res) => {
				console.log(`initView---`, JSON.stringify(res));
				if (res.code === 200) {
					console.log(`initView---`, JSON.parse(res.data));
					let a = JSON.parse(res.data)
					console.log(a.SN)
					this.stateMsg = res['message'];
					this.dataResult = res['data'];
				} else {
					this.dataResult = res['data'];
					this.stateMsg = res['message'];
				}
			});
		},
		/* 获取V系列 */
		getSN() {
			sunmiPrint.getSN((res) => {
				console.log(`getSN--res`, JSON.stringify(res));
				if (res.code === 200) {
					console.log(`getSN--`, JSON.stringify(res));
					this.stateMsg = res['message'];
					this.dataResult = res['data'];
				} else {
					this.dataResult = res['data'];
					this.stateMsg = res['message'];
				}
			});
		},
		connect() {
			//使用前连接打印机
			sunmiPrint.connect((res) => {
				if (res.connect == 'hello') {
					console.log('打印机成功连接');
					this.stateMsg = '打印机成功连接';
					this.dataResult = null;
				}
			});
		},
		disconnect() {
			//使用后断开打印机
			sunmiPrint.disconnect();
		},
		printText() {
			//主动初始化打印机
			sunmiPrint.printInit();
			// 打印一张自检页
			sunmiPrint.printSelfCheck();
			sunmiPrint.printText({
				text: '商米打印测试',
				align: 1,
				size: 30,
				bold: true,
				underline: true,
				compact: true,
				skip: true
			});
		}
	}
};
</script>

<style>
	.hd-container{
		/* padding: 100rpx; */
	}
	.test1{
		position: absolute;
		top: 0;
		left: 0%;
		height: 100rpx;
		background: #fff;
	}
	.test2{
		position: absolute;
		top: 0;
		left: 50%;
		height: 100rpx;
		background: #fff;
	}
	.test3{
		position: absolute;
		top: 170rpx;
		left: 0%;
		height: 100rpx;
		background: #fff;
	}
	.test4{
		position: absolute;
		top: 265rpx;
		left: 50%;
		height: 100rpx;
		background: #fff;
	}
	.hd-btn{
		width: 100rpx;
		/* height: 60rpx; */
		margin-bottom: 30rpx;
		background: #f2f2f2;
	}
	.sun-header{
		width: 100%;
		height: 109px;
		background: #003DB0;
		display: flex;
		justify-content: space-between;
		padding: 0 36rpx;
		padding-top: 110rpx;
		box-sizing: border-box;
		.side{
			width: 58rpx;
			height: 48rpx;
		}
		.logo{
			width: 300rpx;
			height: 56rpx;
		}
		.scan{
			width: 50rpx;
			height: 50rpx;
		}
	}
	
</style>